* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}


.country-list {
  list-style: none;
  display: flex; 
  margin-right: 100px;
}

.country-item {
  background-color: #e4eef8;
  border-radius: 24px;
  padding: 20px; 
  margin-right: 20px; 
}

.country-card {
  background-color: #e4eef8;
  border-radius: 24px;
  padding: 20px; 
  margin-right: 20px; 
}